// font-weight helper:

// 100 Extra Light or Ultra Light;
// 200 Light or Thin; 300 Book or Demi;
// 400 Regular or Normal;
// 500 Medium;
// 600 Semibold or Demibold;
// 700 Bold;
// 800 Black or Extra Bold or Heavy;
// 900 Extra Black or Fat or Ultra Black

@mixin font($f-name, $f-style, $f-weight, $f-url) {
	@font-face {
		font-family: $f-name;
		font-style: $f-style;
		font-weight: $f-weight;
		src: url($f-url) format('woff2');
	}
}

@include font('Roboto Slab', normal, 200, '../fonts/RobotoSlab-Thin.woff2');
@include font('Roboto Slab', normal, 400, '../fonts/RobotoSlab-Regular.woff2');
@include font('Roboto Slab', normal, 700, '../fonts/RobotoSlab-Bold.woff2');
@include font('Open Sans', normal, 400, '../fonts/OpenSans-Regular.woff2');
@include font('Open Sans', italic, 400, '../fonts/OpenSans-Italic.woff2');
