@font-face {
  font-family: "Roboto Slab";
  font-style: normal;
  font-weight: 200;
  src: url("../fonts/RobotoSlab-Thin.woff2") format("woff2");
}

@font-face {
  font-family: "Roboto Slab";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/RobotoSlab-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "Roboto Slab";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/RobotoSlab-Bold.woff2") format("woff2");
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "Open Sans";
  font-style: italic;
  font-weight: 400;
  src: url("../fonts/OpenSans-Italic.woff2") format("woff2");
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  position: relative;
  min-height: 100%;
  font-size: 16px;
}

body {
  width: 100%;
  min-height: 100%;
  padding: 0;
  margin: 0;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
}

img,
embed,
object,
video {
  max-width: 100%;
}

p {
  margin: 1.5625rem 0;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: .9375rem;
  font-weight: 400;
  line-height: 1.5rem;
  letter-spacing: .046875rem;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

.wrapper {
  position: relative;
  min-height: 100%;
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1200px;
  padding: 0 .9375rem;
  margin: 0 auto;
}

.title {
  margin: .9375rem 0;
  font-family: "Roboto Slab", Helvetica, Arial, sans-serif;
  font-size: 3rem;
  font-weight: 700;
  line-height: 2.75rem;
  color: #44444f;
  letter-spacing: .12rem;
}

.title_uppercase {
  text-transform: uppercase;
}

.title_white {
  color: #fff;
}

.subtitle {
  margin-bottom: 1.5625rem;
  font-family: "Roboto Slab", Helvetica, Arial, sans-serif;
  font-size: 1.125rem;
  font-weight: 400;
  color: #65e496;
  letter-spacing: .05625rem;
}

.section {
  padding: 10rem 0;
}

