.author {
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: 1.125rem;
  font-weight: 400;
  line-height: 2.75rem;
  color: #fff;
  letter-spacing: .05625rem;
}

.author_dark {
  color: #696980;
}

.block-author {
  display: flex;
  justify-content: space-between;
}

.block-avatar {
  margin: 3.125rem 3.125rem 0 0;
}

.block-avatar_left {
  margin: 3.125rem 0 0 0;
}

.block-avatar__pic {
  position: relative;
}

.block-avatar__img_left {
  margin-left: 10%;
}

.block-avatar__pattern {
  position: absolute;
  top: 50%;
  left: 60%;
  z-index: -1;
  transform: translate(-50%, -50%);
}

.block-avatar__pattern_left {
  top: -8%;
  left: 0;
  transform: none;
}

.block-blog {
  display: flex;
  width: 100%;
  margin: 5.3125rem 0;
}

.block-blog__list {
  display: flex;
  justify-content: space-between;
  width: 100%;
  column-count: 3;
}

.block-blog__item {
  max-width: 369px;
}

.block-counter {
  margin-top: 10rem;
}

.block-counter__list {
  display: flex;
  justify-content: space-around;
}

.block-counter__item {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.block-counter__value {
  font-family: "Roboto Slab", Helvetica, Arial, sans-serif;
  font-size: 5rem;
  font-weight: 200;
  color: #65e496;
  letter-spacing: .2rem;
}

.block-counter__text {
  font-family: "Roboto Slab", Helvetica, Arial, sans-serif;
  font-size: 1.125rem;
  font-weight: 400;
  color: #696980;
  letter-spacing: .045rem;
}

.block-events {
  flex-basis: 48%;
}

.block-hero {
  display: flex;
}

.block-table {
  margin-bottom: 2.1875rem;
  color: #696980;
}

.block-table__list {
  counter-reset: list;
  column-count: 2;
}

.block-table__item {
  position: relative;
  padding-left: 2.25rem;
  margin-bottom: .5rem;
  line-height: 1.5rem;
  letter-spacing: .04375rem;
  list-style-type: none;
  counter-increment: list;
}

.block-table__item::before {
  content: counter(list) ". ";
}

.block-table__item::after {
  position: absolute;
  top: 1px;
  left: 0;
  padding: 0 .5rem;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: .9375rem;
  color: #fff;
  content: "+";
  background-color: #65e496;
  background-image: linear-gradient(to left, rgba(101, 228, 206, 0.7) 0%, rgba(101, 228, 161, 0.7) 100%);
}

.block-text {
  flex-basis: 50%;
  color: #fff;
}

.block-text_dark {
  color: #696980;
}

.blog-item__img {
  margin-bottom: 1.25rem;
  object-fit: fill;
}

.blog-item__link {
  color: #44444f;
}

.blog-item__link:hover {
  text-decoration: underline;
}

.blog-item__title {
  margin-bottom: .3125rem;
  font-family: "Roboto Slab", Helvetica, Arial, sans-serif;
  font-size: 1.125rem;
  font-weight: 700;
  color: #44444f;
  letter-spacing: .05625rem;
}

.blog-item__info {
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: 1rem;
  color: #696980;
}

.blog-item__info-link {
  font-size: .9375rem;
  color: #65e496;
  letter-spacing: .046875rem;
}

.blog-item__info-link:first-child {
  margin-left: 0.52083rem;
}

.blog-item__info-link::after {
  margin: 0 .3125rem;
  content: '-';
}

.blog-item__info-link:last-child::after {
  display: none;
}

.blog-item__text {
  color: #696980;
}

.book-contents {
  display: flex;
  justify-content: space-between;
  margin-top: 4rem;
}

.book-contents__about {
  flex-basis: 41%;
}

.book-contents__about-desc {
  color: gray;
}

.book-contents__table {
  flex-basis: 50%;
}

.book-contents__title {
  margin-bottom: 1.5625rem;
  font-family: "Roboto Slab", Helvetica, Arial, sans-serif;
  font-size: 1.5rem;
  font-weight: 700;
  color: #44444f;
  letter-spacing: .075rem;
}

.book-hero {
  position: relative;
  flex-basis: 38%;
  margin-right: 3.125rem;
}

.book-hero__picture {
  position: relative;
}

.book-hero__picture-text {
  position: absolute;
  top: 17%;
  left: 2%;
  width: 100%;
  text-align: center;
}

.book-hero__picture-title {
  font-family: "Roboto Slab", Helvetica, Arial, sans-serif;
  font-size: 2.25rem;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: .09rem;
}

.book-hero__picture-subtitle {
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: .875rem;
  color: #65e496;
  letter-spacing: .04375rem;
}

.book-hero__picture-author {
  position: absolute;
  bottom: 10%;
  left: 20%;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: .875rem;
  color: #65e496;
  letter-spacing: .09rem;
}

.book-hero__icon {
  position: relative;
  width: 27.9375rem;
  height: 40.1875rem;
}

.book {
  position: relative;
  width: 269px;
  height: 409px;
  margin-bottom: 2.1875rem;
  border-radius: 2px;
}

.book__text {
  padding: 0 1rem;
  font-size: .9375rem;
  color: #696980;
}

.book__picture {
  width: 100%;
  height: 100%;
}

.book__title {
  font-family: "Roboto Slab", Helvetica, Arial, sans-serif;
  font-size: 1.5rem;
  font-weight: 700;
  color: #44444f;
  letter-spacing: .075rem;
}

.book__inner-text {
  position: absolute;
  top: 16%;
  left: 5%;
  width: 100%;
  text-align: center;
}

.book__inner-title {
  font-family: "Roboto Slab", Helvetica, Arial, sans-serif;
  font-size: 1.5rem;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: .06rem;
}

.book__inner-subtitle {
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: .75rem;
  line-height: 1.5rem;
  color: #65e496;
  letter-spacing: .0375rem;
}

.book__inner-author {
  position: absolute;
  bottom: 10%;
  left: 5%;
  display: block;
  width: 100%;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: .75rem;
  color: #65e496;
  text-align: center;
  letter-spacing: .0375rem;
}

.books {
  margin-top: 5.375rem;
  margin-bottom: 5.625rem;
}

.books__list {
  display: flex;
  justify-content: space-between;
}

.books__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.btn {
  display: inline-block;
  padding: .6875rem .9375rem;
  font-family: "Roboto Slab", Helvetica, Arial, sans-serif;
  font-size: 1.125rem;
  color: #fff;
  letter-spacing: .045rem;
  background: linear-gradient(to left, rgba(101, 228, 206, 0.7) 0%, rgba(101, 228, 161, 0.7) 100%);
  background-color: #65e47d;
  border-radius: 3px;
  box-shadow: 0 2px 0 rgba(101, 228, 125, 0.5);
}

.event {
  letter-spacing: .046875rem;
}

.event__date {
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: .9375rem;
  line-height: 2.75rem;
  color: #696980;
}

.event__title {
  font-family: "Roboto Slab", Helvetica, Arial, sans-serif;
  font-size: 1.125rem;
  font-weight: 700;
  color: #44444f;
  letter-spacing: .05625rem;
}

.event__desc {
  color: #696980;
}

.events {
  margin-top: 1rem;
}

.events__item {
  margin-bottom: 2.5rem;
}

.events__item:last-child {
  margin-bottom: 0;
}

.featured {
  display: flex;
  align-items: center;
}

.featured__title {
  margin-right: 1.5rem;
  font-family: "Roboto Slab", Helvetica, Arial, sans-serif;
  font-size: 1.125rem;
  font-weight: 700;
  color: #44444f;
  letter-spacing: .05625rem;
}

.featured__icon {
  width: 1.125rem;
  height: 1.125rem;
  margin-right: 0.78125rem;
  fill: #65e496;
}

.featured__icon:last-child {
  margin-right: 0;
}

.header {
  position: absolute;
  z-index: 10;
  width: 100%;
  padding: 50px 0;
}

.header__container {
  display: flex;
  justify-content: space-between;
}

.link {
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: .9375rem;
  font-weight: 700;
  color: #65e496;
  text-transform: uppercase;
}

.logo {
  width: 137px;
  height: 28px;
}

.logo__icon {
  max-width: 100%;
  max-height: 100%;
}

.nav {
  display: flex;
  align-items: center;
}

.nav__list {
  display: flex;
}

.nav__item {
  margin-right: 1.25rem;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: 1rem;
  color: #fff;
  letter-spacing: .8px;
}

.note {
  display: block;
  margin: 1.5625rem 0;
}

.section-blog {
  background-color: #f5f6fa;
}

.section-blog__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section-books {
  background-color: #fff;
}

.section-books__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section-contents {
  background-color: #f5f6fa;
}

.section-contents__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.section-events {
  background-color: transparent;
}

.section-events__container {
  display: flex;
  justify-content: space-between;
}

.section-hero {
  position: relative;
  padding-top: 16.25rem;
  background-image: linear-gradient(-60deg, #54b7a1 0%, #426384 100%);
}

.section-hero__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

@media (max-width: 768px) {
  html {
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  html {
    font-size: 13px;
  }
}